// è§¦å‘æ›´æ–°çš„æ–¹æ³•
// 1. ReactDOM.render()  é€šå¸¸åªä¼šåœ¨å…¥å£æ–‡ä»¶æ‰§è¡Œä¸€æ¬¡
// 2. component.setState:
// æ­¥éª¤ï¼š 1). updateQueue.push, 2ï¼‰:scheduleUpdate -> requestIdleCallback-> performWork

class Component {
  constructor(props) {
    this.state = {};
    this.props = props;
  }
  render() {
    // å­ç±»ç»§æ‰¿
  }
  setState(partialState) {
    updateQueue.push({
      from: CLASS_COMPONENT,
      instance: this,
      partialState,
    });
    scheduleUpdate();
  }
}
// ä¿å­˜å®ä¾‹ä¸­fiberçš„å¼•ç”¨
function createInstance(fiber) {
  // å®ä¾‹åŒ–ç»„ä»¶
  let instance;
  if (fiber.type.prototype.render) {
    instance = new fiber.type(fiber.props);
  } else {
    instance = new Didact.Component(fiber.props);
    instance.constructor = fiber.type;
    instance.render = () => fiber.type(fiber.props);
  }
  instance.__fiber = fiber;
  return instance;
}
const Didact = {
  createElement,
  Component,
};

// Didactå…ƒç´  {type,key, props}
// JSX babelè‡ªåŠ¨è°ƒç”¨ ç”ŸæˆDidactå…ƒç´ 
// children: [[{}, {}, {}]] (å½“ä½¿ç”¨mapçš„æ—¶å€™)
// æˆ–è€… [{}, {}]
function createElement(type, config = {}, ...args) {
  const { key, ...props } = { key: '', ...config };
  // const newChildren = children.map();
  // æ’é™¤undefinedå’Œnullã€false
  //<div>hello {name}</div> ä¼šæœ‰ä¸¤ä¸ªchildï¼Œ {name}å¯èƒ½ä¸ºundefined
  // false: {count && <div>hello</div>}
  const hasChildren = args.length > 0;
  const rawChildren = hasChildren ? [].concat(...args) : []; // concatå¯ä»¥åˆå¹¶æ•°ç»„æˆ–å€¼

  props.children = rawChildren
    .filter(c => c != null && c !== false)
    .map(c => (c instanceof Object ? c : createTextElement(c)));
  return {
    type,
    key,
    props,
  };
}
// æ ¼å¼åŒ–æ–‡æœ¬ç±»å‹
function createTextElement(value) {
  // è§„èŒƒæ•°æ®
  return createElement(TEXT_ELEMENT, { nodeValue: value });
}

// ç»„ä»¶è°ƒåº¦æ›´æ–°
// ç±»ä¼¼render
function scheduleUpdate() {
  requestIdleCallback(performWork);
}

function createDomElement(fiber) {
  const isTextElement = fiber.type === TEXT_ELEMENT;
  const dom = isTextElement ? document.createTextNode('') : document.createElement(fiber.type);
  updateDomProperties(dom, [], fiber.props);
  return dom;
}
